@import "../_try-mixins.less";
@import "../_try-variables.less";

.fn-@{try-ns}-common-header{
	background:white;
}

.fn-@{try-ns}-common-title{
 	.swiper{
		border-top: 1px solid #dedede ;
		background: @try-theme-color;
	}

	.item{
		padding: 0.24rem 0;
		width: 25%;
		display: block;
		text-align: center;
		color: #fff;

		> i{
			font-size: 0.9rem;
			display: block;
		}

		> span{
			padding-top: 0.04rem;
			font-size: @font-size-base;
			display: block;
		}

		&-active{
			background: #19AB95;
		}
	}
}
