@import "../_try-mixins.less";
@import "../_try-variables.less";

.fn-@{try-ns}-prolist{
	margin: 0.2rem 0px;
	padding: 0.4rem 0.2rem;
	border-left: 0.2rem solid @try-theme-color;;
	background: #fff;

	.list-hd{
		color: @try-theme-color;
		font-size: (@font-size-lg)-0.05rem;
		line-height: 0.88rem;
		font-weight: 600;

		.hd-more{
			float: right;
			font-weight: 600;
		};

		.hd-more-icon{
			float: right;
			font-size: @font-size-sm;
			padding: 0.2rem;
			color: #fff;
			background: @try-theme-color;
			border-radius: 2px;
		};
	};
};

.list-wrap{
	width: 100%;
	overflow: hidden;
	.list-item{
		width: 49.5%;
		float: left;
		background: #fff;

		&:nth-child(2n){
			margin-left: 1%;
		};

		&:nth-child(n+3){
			margin-top: 0.2rem;
		};

	.list-img{
		.size(100%,100%);
	};

	.list-title{
		.ellipsis(2);
		font-size: 0.56rem;
		padding: 0 0.32rem;
		margin-bottom: 0.2rem;
	}

	.list-price{
		padding: 0.26rem 0.32rem 0.1rem;
		color: #999;
		font-size: @font-size-sm;

		>i{
			font-style: normal;
			padding: 0px 0.2rem;
			color: #c20053;
		};

		>span{
			text-decoration: line-through;
			float: right;
			vertical-align: center;
		}
	}
	};
}
